/* RestPages.css */

.container {
    padding: 20px;
  }
  
  .row {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .col-md-6 {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  
  .img-fluid {
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
  }
  
  @media (max-width: 768px) {
    .col-md-6 {
      width: 100%;
    }
  }
  