/* ===========Register Page========== */
.form-container {
  margin-top: 20px;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  border-radius: 10px;
}

.form-container form {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: 0 0 10px rgba(5, 5, 5, 0.8);
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
}

.form-container form input {
  border: none;
  border-bottom: 1px solid #000;
  border-radius: 0;
  background-color: transparent;
  margin-bottom: 15px;
  padding: 5px;
  transition: border-color 0.3s ease-in-out;
}

.form-container form input:focus {
  border-color: #333; /* Change border color on focus */
}

.form-container form button {
  border: 1px solid black;
  border-radius: 0px;
  background-color: #000;
  color: white;
  width: 250px;
  padding: 10px;
  transition: background-color 0.3s ease-in-out;
}

.form-container form button:hover {
  border: 1px solid black;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #434343,
    #000000
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #434343,
    #000000
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
/* ===========Register Page========== */

.forgot-btn {
  background-color: #271f30c4 !important;
  border: 0 !important;
  transition: background-color 0.3s ease-in-out;
}

.forgot-btn:hover {
  color: white !important;
}

/* ============Profile Dashboard Form CSS============ */
.form-container h4.title {
  text-align: center;
  margin-bottom: 20px;
  font-weight: bold;
}

.form-container input[type="text"],
.form-container input[type="email"],
.form-container input[type="password"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: none;
  border-bottom: 1px solid #ccc; /* Add bottom border */
  background-color: transparent;
  transition: border-bottom-color 0.3s ease-in-out;
}

.form-container input[type="text"]:focus,
.form-container input[type="email"]:focus,
.form-container input[type="password"]:focus {
  border-bottom-color: #333; /* Change border color on focus */
}

.form-container button {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: rgba(0, 123, 255, 0.7) !important; /* Blue button color */
  color: #fff !important;
  transition: background-color 0.3s ease-in-out; /* Smooth transition for background color */
}

.form-container button:hover {
  background-color: #0056b3; /* Darker blue on hover */
}
/* ============Profile Dashboard Form CSS============ */
